import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormControl } from '@angular/forms';
import { BehaviorSubject, Subscription } from 'rxjs';
import { take } from 'rxjs/operators';
import { FIXTURE_MODULE_ROUTES, generateFullFixtureModuleRoute } from '@sw-ui-components/modules/fixture/fixture.routes';

import { EventsService, IEvent, ILeague, IUser, UserService } from '@aft-core';
import { generateFullPrivateModuleRoute, PRIVATE_MODULE_ROUTES } from '@scouting/private/private.routes';
import { HomeService } from '@scouting/private/services/home.service';

@Component({
  selector: 'aft-scout-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
})
export class HomeComponent implements OnInit, OnDestroy {
  user$: BehaviorSubject<IUser | undefined>;
  leagues$: BehaviorSubject<ILeague[]>;
  selectedLeague$: BehaviorSubject<ILeague | undefined>;

  leaguePageRoute: Array<string | number> = generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.LEAGUE);
  teamPageRoute: Array<string | number> = generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.TEAM_DETAILS);
  playerPageRoute: Array<string | number> = generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.PLAYER);
  advSearchPageRoute: Array<string | number> = generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.ADVANCED_SEARCH);
  fixturePageRoute: Array<string | number> = generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.FIXTURE);
  favoriteCount$ = new BehaviorSubject<number | string>('-');
  leagueEvents$: BehaviorSubject<Array<IEvent>>;

  private readonly _leagueControl: FormControl;
  private readonly _subscriptions: Subscription[] = [];

  constructor(
    private _fb: FormBuilder,
    private _router: Router,
    private _homeService: HomeService,
    private _userService: UserService,
    private _eventsService: EventsService
  ) {
    this.user$ = this._userService.loadedProfile$;
    this.leagues$ = this._homeService.leagues$;
    this.selectedLeague$ = this._homeService.selectedLeague$;
    this._leagueControl = this._fb.control(this.selectedLeague$.value);
    this.leagueEvents$ = this._eventsService.events$;
  }

  ngOnInit(): void {
    this._userService.favorites$.pipe(take(2)).subscribe(favoritesList => {
      this.favoriteCount$.next(favoritesList?.favoriteCount || 0);
    });
    // Pre selects the previously saved league (if any). First Value its undefined
    this.selectedLeague$.pipe(take(2)).subscribe(selectedLeague => {
      // Prevents first undefined value
      if (selectedLeague) {
        this._leagueControl.setValue(selectedLeague);
      }
    });

    this._subscriptions.push(
      this.leagueCtrl.valueChanges.subscribe(league => {
        this._eventsService.loadLeagueEvents(league.id);
        this._homeService.changeSelectedLeague(league);
      })
    );
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(x => x.unsubscribe());
  }

  goToAdvancedSearch(): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.ADVANCED_SEARCH));
  }

  goToFavorites(): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.FAVORITES));
  }

  get leagueCtrl(): FormControl {
    return this._leagueControl;
  }
}
