import { Component, Input, OnInit } from '@angular/core';
import { EventsService, ITeamMatch, IEvent, IEventScore, IEventPeriodScore, EVENT_MATCH_OFFSETS, IVideo, MediaService } from '@aft-core';
import { CustomTranslatePipe } from '@sw-ui-components'; 
import { TranslateService } from '@ngx-translate/core';
import { Observable, Subject } from 'rxjs';
import { Router } from '@angular/router';
import { generateFullPrivateModuleRoute as generateScoutingRoutes } from '@scouting/private/private.routes';
import { PRIVATE_MODULE_ROUTES as SCOUTING_ROUTES } from '@scouting/private/private.routes';

@Component({
  selector: 'aft-detail-event-score',
  templateUrl: './detail-event-score.component.html',
  styleUrls: ['./detail-event-score.component.scss'],
  providers: [EventsService],
})
export class DetailEventScoreComponent implements OnInit {
  
  @Input() eventDetails: IEvent[] | undefined;

  @Input() homeTeam: ITeamMatch | undefined;

  @Input() awayTeam: ITeamMatch | undefined;

  public showModal:boolean=false;
  public listEventScore:IEventScore[]=[];
  public listEventPeriodScore:IEventPeriodScore[]=[];
  public dataOverlayPanel:any;
  public titleOverlayPanel:any='';
  public videoUrl: string = '';
  public showVideoBox: boolean = false;

  constructor(
    private customTranslatePipe: CustomTranslatePipe,
    private _translate: TranslateService,
    private _mediaService: MediaService,
    private _router: Router
  ) { }

  public get _eventTotalScore(){
    return { type:'', home:{ score:0, data:[] }, away:{ score:0, data:[] } } as IEventScore;
  }

  public get _eventPeriodScore(){
    return { period:'', label:'', home:[], away:[]} as IEventPeriodScore;
  }

  ngOnInit(): void {
    // Added all type of details whitin data match event
    if( this.eventDetails !== undefined){
      this.loadListsEventScores( this.eventDetails )
    }
  }
  
  loadListsEventScores( eventDetails:IEvent[]){

    let auxTotalScore:any=[];
    let auxPeriodScore:any=[];

    eventDetails.map( (event:IEvent) => {

      // LOAD SCORE TOTALS ( SECCTION 1)
      if(!auxTotalScore.includes(event.typeLabel)){
        const {...iEventTotalScore} = this.addListScoreTotal(event);
        this.listEventScore.push({...iEventTotalScore})
        auxTotalScore.push(event.typeLabel);
      } else {
        this.listEventScore = this.setListScoreTotal(event);
      };

      // LOAD SCORE PERIODS ( SECCTION 2)
      if(event.type.primary !== 'duel'){
        if(!auxPeriodScore.includes(event.matchPeriod)){
          const {...iEventPeriodScore} = this.addListScorePeriod(event);
          this.listEventPeriodScore.push({...iEventPeriodScore})
          auxPeriodScore.push(event.matchPeriod);
        } else {
          this.listEventPeriodScore = this.setListScorePeriod(event);
        };
      }
      
    });
  }

  addListScoreTotal(event:IEvent){
      let iEventTotalScore = this._eventTotalScore;
      iEventTotalScore.type = event.typeLabel;
      iEventTotalScore.home.score = (event.teamId === this.homeTeam?.id ) ? 1 : 0;
      iEventTotalScore.home.data = (event.teamId === this.homeTeam?.id ) ? [event] : [];
      iEventTotalScore.away.score = (event.teamId === this.awayTeam?.id ) ? 1 : 0;
      iEventTotalScore.away.data = (event.teamId === this.awayTeam?.id ) ? [event] : [];
      return iEventTotalScore;
  }

  setListScoreTotal(event:IEvent){
      return this.listEventScore.map( (eventScore:IEventScore) =>{
        if( eventScore.type === event.typeLabel){
          if( event.teamId === this.homeTeam?.id ){
            eventScore.home.score = eventScore.home.score + 1;
            eventScore.home.data.push( event );
          } else if( event.teamId === this.awayTeam?.id ){
            eventScore.away.score = eventScore.away.score + 1;
            eventScore.away.data.push( event );
          }
        }
        return eventScore;
      })
  }

  addListScorePeriod(event:IEvent){
      let iEventPeriodScore = this._eventPeriodScore;
      iEventPeriodScore.period = event.matchPeriod;
      iEventPeriodScore.home = (event.teamId === this.homeTeam?.id ) ? [event] : [];
      iEventPeriodScore.away = (event.teamId === this.awayTeam?.id ) ? [event] : [];
      
      return iEventPeriodScore;
  }

  setListScorePeriod(event:IEvent){
    return this.listEventPeriodScore.map( (eventPeriodScore:IEventPeriodScore) =>{
      if( eventPeriodScore.period === event.matchPeriod){
        if( event.teamId === this.homeTeam?.id ){
          eventPeriodScore.home.push(event);
        } else if( event.teamId === this.awayTeam?.id ){
          eventPeriodScore.away.push(event)
        }
      }
      return eventPeriodScore;
    })
  }

  openOverlayPanel(data:any,type:string){
    this.translated('EVENTS.TYPE.'+type).subscribe((r)=>this.titleOverlayPanel=r);
    this.dataOverlayPanel = data;
    this.showModal=true;
  }

  goToPlayerDetails(playerId: number | undefined): void {
    if (playerId) {
      this._router.navigate(generateScoutingRoutes(SCOUTING_ROUTES.PLAYER, playerId));
    }
  }

  showVideo(matchId:number,videoTimestamp:number) {
    this._mediaService.getVideoUrl(matchId, videoTimestamp).subscribe(
      (videoData: IVideo) => {
        this.videoUrl = videoData.urlVideo;
        this.showVideoBox = true;
      },
      (error: any) => {
        console.error('Error al obtener la url del video: ', error);
      }
    );
  }

  ngOnDestroy(): void {
   
  }

  getPeriodScoreLabel(text:string){
    return text;
  }

  translated(text:string): Observable<string> {
    var subject = new Subject<string>();
    this.customTranslatePipe.transform(text.toUpperCase(), this._translate.currentLang).subscribe(r => subject.next(r) );
    return subject.asObservable();
  }

  translatedPeriods(matchPeriod:string){
    switch (matchPeriod) {
      case EVENT_MATCH_OFFSETS.FIRST_HALF:
        return 'MATCH_DETAIL.FIRST_HALF';
      case EVENT_MATCH_OFFSETS.SECOND_HALF:
        return 'MATCH_DETAIL.SECOND_HALF';
      case EVENT_MATCH_OFFSETS.FIRST_STRETCH:
        return 'MATCH_DETAIL.FIRST_HALF_EXTRA';
      case EVENT_MATCH_OFFSETS.SECOND_STRETCH:
        return 'MATCH_DETAIL.SECOND_HALF_EXTRA';
      // Penalties
      default:
        return 'MATCH_DETAIL.PENALTIES';
    }
  }

}
