import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { BehaviorSubject, Subscription } from 'rxjs';
import { AppConfigService, ITeam, PLATFORM_ENUM, TeamsPlayersService } from '@aft-core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { Router } from '@angular/router';
import { FIXTURE_MODULE_ROUTES, generateFullFixtureModuleRoute } from '@sw-ui-components/modules/fixture/fixture.routes';

@Component({
  selector: 'aft-team-list',
  templateUrl: './team-list.component.html',
  styleUrls: ['./team-list.component.scss'],
})
export class TeamListComponent implements OnInit, OnDestroy {
  @Input()
  hiddenColumns: string[] | undefined;

  loading$: BehaviorSubject<boolean>;
  form: FormGroup;
  filteredTeams: Array<ITeam>;

  showAlert = true;
  isBackofficeProfile: boolean = false;

  private _teams: Array<ITeam>;
  private _subscriptions: Subscription[] = [];

  constructor(
    private _fb: FormBuilder,
    private _teamsService: TeamsPlayersService,
    private _translateService: TranslateService,
    private _router: Router,
    private _appConfigService: AppConfigService
  ) {
    this.loading$ = this._teamsService.isLoading$;
    this._teams = this._teamsService.teams$.value;
    this.filteredTeams = this._teamsService.teams$.value;

    this.form = this._fb.group({
      searchString: [''],
    });

    this.isBackofficeProfile = this._appConfigService.appConfig.platform === PLATFORM_ENUM.BACKOFFICE;
  }

  ngOnInit(): void {
    this._subscriptions.push(
      this._teamsService.teams$.subscribe(teams => {
        this._teams = teams;
        this.filteredTeams = teams;
      })
    );

    this._subscriptions.push(
      this.form.controls.searchString.valueChanges.subscribe(newValue => {
        if (newValue?.length) {
          // Filters list based on the search string
          this.filteredTeams = this._teams.filter((team: ITeam) => {
            const lowerCaseStringVal = newValue.toLowerCase();
            const gender = team.gender.toUpperCase();
            const genderTranslation = this._translateService.instant(`GENERAL.GENDER.${gender}`);

            return (
              team.name.toLowerCase().includes(lowerCaseStringVal) ||
              team.city?.toLowerCase().includes(lowerCaseStringVal) ||
              genderTranslation.toLowerCase().includes(lowerCaseStringVal)
            );
          });
        }
      })
    );
  }

  // Destroys subscriptions and clears service
  ngOnDestroy(): void {
    this._subscriptions.forEach(sub => sub.unsubscribe());
  }

  isHiddenColumn(columnName: string): boolean {
    return this.hiddenColumns?.includes(columnName) || false;
  }

  // Hides sync alert
  hideAlert(): void {
    this.showAlert = false;
  }

  goToTeamDetails(team: ITeam): void {
    if (!this.isBackofficeProfile) {
      this._router.navigate(generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.TEAM_DETAILS, team.id));
    }
  }
}
