import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PrivateCanActivateChildGuard, PrivateGuard } from '@aft-core';

import { PRIVATE_MODULE_ROUTES } from './private.routes';
import { HomeComponent } from './components/private-layout/components/home/home.component';
import { PrivateLayoutComponent } from './components/private-layout/private-layout.component';
import { FavoritesListComponent } from '@scouting/private/components/private-layout/components/favorites-list/favorites-list.component';

const routes: Routes = [
  {
    path: PRIVATE_MODULE_ROUTES.HOME,
    component: PrivateLayoutComponent,
    canActivate: [PrivateGuard],
    canActivateChild: [PrivateCanActivateChildGuard],
    children: [
      {
        path: PRIVATE_MODULE_ROUTES.HOME,
        component: HomeComponent,
      },
      {
        path: PRIVATE_MODULE_ROUTES.PROFILE,
        // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
        loadChildren: () => import('@sw-ui-components/modules/profile/profile.module').then(p => p.ProfileModule),
      },
      {
        path: PRIVATE_MODULE_ROUTES.ADVANCED_SEARCH,
        // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
        loadChildren: () => import('./advanced-search/advanced-search.module').then(a => a.AdvancedSearchModule),
      },
      {
        path: PRIVATE_MODULE_ROUTES.PLAYER,
        // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
        loadChildren: () => import('./player/player.module').then(p => p.PlayerModule),
      },
      {
        path: PRIVATE_MODULE_ROUTES.FAVORITES,
        component: FavoritesListComponent,
      },
      {
        path: PRIVATE_MODULE_ROUTES.LEAGUE_ROUTE,
        // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
        loadChildren: () => import('./league/league.module').then(l => l.LeagueModule),
      },
      {
        path: PRIVATE_MODULE_ROUTES.FIXTURE,
        // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
        loadChildren: () => import('@sw-ui-components/modules/fixture/fixture.module').then(f => f.FixtureModule),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PrivateRoutingModule {}
