import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { FIXTURE_MODULE_ROUTES } from './fixture.routes';
import { FixtureListComponent } from './components/fixture-list/fixture-list.component';
import { MatchDetailComponent } from './components/match-detail/match-detail.component';
import { TeamDetailComponent } from './components/team-detail/team-detail.component';

const routes: Routes = [
  {
    path: FIXTURE_MODULE_ROUTES.FIXTURE,
    component: FixtureListComponent,
  },
  {
    path: FIXTURE_MODULE_ROUTES.FIXTURE_FILTERED_ROUTE,
    component: FixtureListComponent,
  },
  {
    path: FIXTURE_MODULE_ROUTES.FIXTURE_SEMI_FILTERED_ROUTE,
    component: FixtureListComponent,
  },
  {
    path: FIXTURE_MODULE_ROUTES.MATCH_ROUTE,
    component: MatchDetailComponent,
  },
  {
    path: FIXTURE_MODULE_ROUTES.TEAM_ROUTE,
    component: TeamDetailComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FixtureRoutingModule {}
