import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Location } from '@angular/common';
import {
  APP_ROUTES,
  ENTITIES_ENUM,
  EventsService,
  IEvent,
  ILeague,
  IPlayer,
  ITeamInfo,
  LeaguesService,
  TeamsPlayersService,
} from '@aft-core';
import { BehaviorSubject, Subscription } from 'rxjs';
import { take } from 'rxjs/operators';

@Component({
  selector: 'aft-team-detail',
  templateUrl: './team-detail.component.html',
  styleUrls: ['./team-detail.component.scss'],
})
export class TeamDetailComponent implements OnInit, OnDestroy {
  teamId = 0;
  selectedTab = 0;
  league$ = new BehaviorSubject<ILeague | undefined>(undefined);

  players$: BehaviorSubject<Array<IPlayer>>;
  teamInfo$: BehaviorSubject<ITeamInfo | undefined>;
  teamEvents$: BehaviorSubject<Array<IEvent>>;

  entitiesEnum = ENTITIES_ENUM;

  private _subscriptions: Array<Subscription> = [];

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _location: Location,
    private _leagueService: LeaguesService,
    private _teamsAndPlayersService: TeamsPlayersService,
    private _eventService: EventsService
  ) {
    this.players$ = this._teamsAndPlayersService.players$;
    this.teamInfo$ = this._teamsAndPlayersService.teamInfo$;
    this.teamEvents$ = this._eventService.events$;
  }

  ngOnInit(): void {
    this._route.params.subscribe((params: Params) => {
      if (params.teamId) {
        this.teamId = params.teamId;
        // Reloads team data when param changes and component its already initialized
        if (this._leagueService.leagues$.value?.length) {
          this._loadTeamData();
        }
      } else {
        this._router.navigate(APP_ROUTES.PRIVATE);
      }
    });

    this._leagueService.leagues$.pipe(take(2)).subscribe(leagues => {
      if (leagues.length) {
        this._loadTeamData();
      }
    });

    this._subscriptions.push(
      this.teamInfo$.subscribe(teamInfo => {
        if (teamInfo) {
          const leagueList = this._leagueService.leagues$.value;
          const teamLeague = leagueList.find(l => l.id === teamInfo.leagueId);

          if (teamLeague?.id !== this.league$.value?.id) {
            this.league$.next(teamLeague);
          }
        }
      })
    );
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(x => x.unsubscribe());
  }

  goBack(): void {
    this._location.back();
  }

  private _loadTeamData(): void {
    this._teamsAndPlayersService.loadTeamInfoById(this.teamId);
    this._teamsAndPlayersService.loadPlayersByTeamId(this.teamId);
    this._eventService.loadTeamEvents(this.teamId);
  }
}
