import { Component, Input, OnInit } from '@angular/core';
import { BehaviorSubject, Subscription } from 'rxjs';
import {
  FixtureService,
  IFixture,
  IFixtureDate,
  ILeague,
  IMatch,
  ITeamMatch,
  MATCH_STATUS_ENUM,
  MessagingService,
} from '@aft-core';
import { Router } from '@angular/router';
import { map } from 'rxjs/operators';
import { FIXTURE_MODULE_ROUTES, generateFullFixtureModuleRoute } from '@sw-ui-components/modules/fixture/fixture.routes';

@Component({
  selector: 'aft-fixture-widget',
  templateUrl: './fixture-widget.component.html',
  styleUrls: ['./fixture-widget.component.scss'],
})
export class FixtureWidgetComponent implements OnInit {
  @Input()
  selectedLeague: BehaviorSubject<ILeague | undefined> | undefined;
  fixture$: BehaviorSubject<IFixture | undefined>;
  activeFixture: IFixtureDate | undefined;

  private _subscriptions: Array<Subscription> = [];

  seasonDates$: BehaviorSubject<Array<IFixtureDate>>;

  constructor(private _fixtureService: FixtureService, private _messagingService: MessagingService, private _router: Router) {
    this.fixture$ = this._fixtureService.fixture$;
    this.seasonDates$ = this._fixtureService.seasonDates$;
  }

  ngOnInit(): void {
    if (this.selectedLeague) {
      this._subscriptions.push(
        this.selectedLeague.subscribe(league => {
          if (league) {
            this.loadFixtureData(league);
          }
        })
      );
    } else {
      throw new Error('League parameter not recieved by input');
    }

    this._subscriptions.push(
      this._fixtureService.seasonDates$
        .pipe(
          map(dates => {
            return dates?.filter(date => date.actual)[0];
          })
        )
        .subscribe(date => {
          this.activeFixture = date;
        })
    );
  }

  loadFixtureData(league: ILeague): void {
    const activeSeason = league.seasons.find(x => x.active);
    if (activeSeason) {
      this._fixtureService.loadSeasonDates(activeSeason.id);
      this._fixtureService.loadFixtureInfoByDate(activeSeason.id, undefined, undefined, true);
    } else {
      this._messagingService.info('League doesnt have active season', 'League doesnt have active season');
    }
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(x => x.unsubscribe());
  }

  goToFixture(): void {
    if (this.selectedLeague?.value?.id && this.activeFixture?.dateId) {
      this._router.navigate(
        generateFullFixtureModuleRoute(
          FIXTURE_MODULE_ROUTES.FIXTURE_FILTERED,
          this.selectedLeague?.value?.id,
          this.activeFixture?.dateId
        )
      );
    } else if (this.selectedLeague?.value?.id && !this.activeFixture?.dateId) {
      this._router.navigate(
        generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.FIXTURE_FILTERED, this.selectedLeague?.value?.id)
      );
    }
  }

  goToTeam(team: ITeamMatch | undefined): void {
    if (team?.id) {
      this._router.navigate(generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.TEAM_DETAILS, team.id));
    }
  }

  goToMatchDetails(match: IMatch): void {
    if (match.status === MATCH_STATUS_ENUM.PLAYED) {
      this._router.navigate(generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.MATCH_DETAILS, match.id));
    }
  }
}
