import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormControl } from '@angular/forms';
import { BehaviorSubject, merge, Observable, Subscription } from 'rxjs';
import { FixtureService, ILeague, LeaguesService, LoadingService } from '@aft-core';
import { ActivatedRoute } from '@angular/router';
import { take } from 'rxjs/operators';

@Component({
  selector: 'aft-fixture-list',
  templateUrl: './fixture-list.component.html',
  styleUrls: ['./fixture-list.component.scss'],
})
export class FixtureListComponent implements OnInit, OnDestroy {
  isLoading$: Observable<boolean>;
  leagues$: BehaviorSubject<Array<ILeague>>;
  selectedLeague: FormControl;
  selectedLeague$: BehaviorSubject<ILeague | undefined>;
  private _subscriptions: Array<Subscription> = [];

  constructor(
    private _fb: FormBuilder,
    private _leaguesService: LeaguesService,
    private _fixtureService: FixtureService,
    private _loadingService: LoadingService,
    private _route: ActivatedRoute
  ) {
    this.leagues$ = this._leaguesService.leagues$;
    this.isLoading$ = merge(this._leaguesService.isLoading$, this._loadingService.loading$);
    this.selectedLeague = this._fb.control({ value: null, disabled: false });
    this.selectedLeague$ = new BehaviorSubject<ILeague | undefined>(undefined);
  }

  ngOnInit(): void {
    this._leaguesService.leagues$.pipe(take(2)).subscribe(leagues => {
      if (leagues.length) {
        this._subscriptions.push(
          this.selectedLeague.valueChanges.subscribe((league: ILeague) => {
            if (league) {
              this.selectedLeague$.next(league);
            } else {
              this.selectedLeague$.next(undefined);
              this._fixtureService.resetService();
            }
          }),
          this._route.params.subscribe(params => {
            if (params.leagueId) {
              this.selectedLeague.setValue(this._leaguesService.findLeagueById(params.leagueId));
            }
          })
        );
      }
    });
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(x => x.unsubscribe());
    this._fixtureService.resetService();
  }
}
