import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { BehaviorSubject, Subscription } from 'rxjs';
import {
  APP_ROUTES,
  EVENT_MATCH_OFFSETS,
  EventsService,
  FixtureService,
  IEvent,
  ILeague,
  IMatch,
  ISeason,
  ITeamMatch,
  LeaguesService,
  MATCH_STATUS_ENUM,
  TMatchStatus,
} from '@aft-core';
import { Location } from '@angular/common';
import { take } from 'rxjs/operators';

@Component({
  selector: 'aft-match-detail',
  templateUrl: './match-detail.component.html',
  styleUrls: ['./match-detail.component.scss'],
  providers: [EventsService],
})
export class MatchDetailComponent implements OnInit {
  match$ = new BehaviorSubject<IMatch | undefined>(undefined);
  matchEvents$: BehaviorSubject<Array<IEvent>>;

  matchLeague: ILeague | undefined;
  matchSeason: ISeason | undefined;
  homeTeam: ITeamMatch | undefined;
  awayTeam: ITeamMatch | undefined;

  eventDetails: IEvent[] = [];
  firstHalfEvents: Array<IEvent> = [];
  secondHalfEvents: Array<IEvent> = [];
  firstExtraEvents: Array<IEvent> = [];
  secondExtraEvents: Array<IEvent> = [];
  penaltiesEvents: Array<IEvent> = [];

  private _subscriptions: Array<Subscription> = [];

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _fixtureService: FixtureService,
    private _eventsService: EventsService,
    private _locationService: Location,
    private _leagueService: LeaguesService
  ) {
    this.matchEvents$ = this._eventsService.events$;
  }

  ngOnInit(): void {
    this._route.params.subscribe((params: Params) => {
      if (params.matchId) {
        this._fixtureService.loadMatchesInfo([params.matchId], true);
        this._eventsService.loadMatchEvents(params.matchId);
        //this._eventsService.loadMatchEventsAdvance(params.matchId);
      } else {
        this._router.navigate(APP_ROUTES.PRIVATE);
      }
    });

    this._leagueService.leagues$.pipe(take(2)).subscribe(leagues => {
      if (leagues?.length) {
        this._subscriptions.push(
          this._fixtureService.matches$.subscribe(matches => {
            if (matches?.length) {
              const match = matches[0];
              this.match$.next(match);
              this.matchLeague = leagues.find(l => l.id === match.leagueId);
              this.matchSeason = this.matchLeague?.seasons?.find(s => s.active);
              this.homeTeam = match.homeTeam;
              this.awayTeam = match.awayTeam;
            }
          })
        );
      }
    });

    this._subscriptions.push(
      this.matchEvents$.subscribe(events => {
        this._clearArrays();
        if (events.length) {
          events = this._sortEventsByTime(events);
          this.eventDetails = events;
          events.forEach(evt => {
            if (evt.type.primary === 'duel') {
              return;
            }
            switch (evt.matchPeriod) {
              case EVENT_MATCH_OFFSETS.FIRST_HALF:
                this.firstHalfEvents.push(evt);
                break;
              case EVENT_MATCH_OFFSETS.SECOND_HALF:
                this.secondHalfEvents.push(evt);
                break;
              case EVENT_MATCH_OFFSETS.FIRST_STRETCH:
                this.firstExtraEvents.push(evt);
                break;
              case EVENT_MATCH_OFFSETS.SECOND_STRETCH:
                this.secondExtraEvents.push(evt);
                break;
              default:
                // Penalties
                this.penaltiesEvents.push(evt);
                break;
            }
          });
        }
      })
    );
  }

  resolveStatusClass(matchStatus: TMatchStatus): string {
    switch (matchStatus) {
      case MATCH_STATUS_ENUM.FIXTURE:
        return 'green';
      case MATCH_STATUS_ENUM.PLAYED:
        return 'dark_blue';
      case MATCH_STATUS_ENUM.PLAYING:
        return 'live_red';
      case MATCH_STATUS_ENUM.SUSPENDED:
        return 'light_blue';
      default:
        return '';
    }
  }

  ngOnDestroy(): void {
    this._clearArrays();
    this._subscriptions.forEach(sub => sub.unsubscribe());
  }

  goBack(): void {
    this._locationService.back();
  }

  private _clearArrays() {
    this.firstHalfEvents = [];
    this.secondHalfEvents = [];
    this.firstExtraEvents = [];
    this.secondExtraEvents = [];
    this.penaltiesEvents = [];
  }

  private _sortEventsByTime(events: Array<IEvent>): Array<IEvent> {
    return events.sort((a, b) => {
      if (a.minute < b.minute) {
        return -1;
      } else if (a.minute > b.minute) {
        return 1;
      } else {
        if (a.second < b.second) {
          return -1;
        } else if (a.second > b.second) {
          return 1;
        } else {
          return 0;
        }
      }
    });
  }
}