import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { FixtureService } from '@aft-core';

import { FixtureRoutingModule } from './fixture-routing.module';
import { PipesModule } from '../../pipes.module';
import { PrimengModule } from '../../primeng.module';
import { UiComponentsModule } from '../../ui-components.module';
import { FixtureListComponent } from './components/fixture-list/fixture-list.component';
import { FixtureDetailComponent } from './components/fixture-detail/fixture-detail.component';
import { MatchDetailComponent } from './components/match-detail/match-detail.component';
import { TeamListComponent } from './components/team-list/team-list.component';
import { TeamDetailComponent } from './components/team-detail/team-detail.component';
import { FixtureWidgetComponent } from './components/fixture-widget/fixture-widget.component';
import { DetailEventScoreComponent } from './components/match-detail/components/detail-event-score/detail-event-score.component';


const components = [
  FixtureListComponent,
  FixtureDetailComponent,
  MatchDetailComponent,
  TeamListComponent,
  TeamDetailComponent,
  FixtureWidgetComponent,
  DetailEventScoreComponent
];

@NgModule({
  declarations: [...components],
  exports: [...components],
  imports: [CommonModule, ReactiveFormsModule, PrimengModule, UiComponentsModule, PipesModule, FixtureRoutingModule],
  providers: [FixtureService],
})
export class FixtureModule {}
