import { Component, OnDestroy, OnInit } from '@angular/core';
import {
  APP_ROUTES,
  ENTITIES_ENUM,
  IUserFavoriteLeague,
  IUserFavoritePlayer,
  IUserFavoriteTeam,
  LeaguesService,
  UserService,
} from '@aft-core';
import { BehaviorSubject, of, Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { generateFullPrivateModuleRoute, PRIVATE_MODULE_ROUTES } from '@scouting/private/private.routes';
import { FIXTURE_MODULE_ROUTES, generateFullFixtureModuleRoute } from '@sw-ui-components/modules/fixture/fixture.routes';

@Component({
  selector: 'aft-scout-favorites-list',
  templateUrl: './favorites-list.component.html',
  styleUrls: ['./favorites-list.component.scss'],
})
export class FavoritesListComponent implements OnInit, OnDestroy {
  players$ = new BehaviorSubject<IUserFavoritePlayer[]>([]);
  teams$ = new BehaviorSubject<IUserFavoriteTeam[]>([]);
  leagues$ = new BehaviorSubject<IUserFavoriteLeague[]>([]);

  entitiesEnum = ENTITIES_ENUM;

  private _subscriptions: Subscription[] = [];

  constructor(private _userService: UserService, private _router: Router, private _leagueService: LeaguesService) {}

  ngOnInit(): void {
    this._subscriptions.push(
      this._userService.favorites$.subscribe(favList => {
        this.players$.next(favList?.players || []);
        this.teams$.next(favList?.teams || []);
        this.leagues$.next(favList?.leagues || []);
      })
    );
  }

  findLeagueNameById(id: number): string {
    const leagues = this._leagueService.leagues$.value;
    return leagues?.find(l => l.id === id)?.name || 'Not found';
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(s => s.unsubscribe());
  }

  goBack(): void {
    this._router.navigate(APP_ROUTES.PRIVATE);
  }

  goToPlayer(id: number): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.PLAYER, id));
  }

  goToTeam(id: number): void {
    this._router.navigate(generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.TEAM_DETAILS, id));
  }

  goToLeague(id: number): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.LEAGUE, id));
  }
}
