import { Component, Input } from '@angular/core';
import { ILeague } from '@aft-core';
import { Router } from '@angular/router';
import { generateFullPrivateModuleRoute, PRIVATE_MODULE_ROUTES } from '@scouting/private/private.routes';

@Component({
  selector: 'aft-scout-home-league',
  templateUrl: './home-league.component.html',
  styleUrls: ['./home-league.component.scss'],
})
export class HomeLeagueComponent {
  @Input()
  league: ILeague | undefined;

  constructor(private _router: Router) {}

  goToLeague(): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.LEAGUE, this.league?.id));
  }
}
