import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { BehaviorSubject, merge, Observable, Subscription } from 'rxjs';
import { map } from 'rxjs/operators';
import {
  FixtureService,
  IFixture,
  IFixtureDate,
  ILeague,
  LeaguesService,
  LoadingService,
  TMatchStatus,
  MATCH_STATUS_ENUM,
  MessagingService,
  IMatch,
  ITeamMatch,
  AppConfigService,
  PLATFORM_ENUM,
} from '@aft-core';
import { FIXTURE_MODULE_ROUTES, generateFullFixtureModuleRoute } from '@sw-ui-components/modules/fixture/fixture.routes';

@Component({
  selector: 'aft-fixture-details',
  templateUrl: './fixture-detail.component.html',
  styleUrls: ['./fixture-detail.component.scss'],
})
export class FixtureDetailComponent implements OnInit, OnDestroy {
  @Input()
  selectedLeague$: BehaviorSubject<ILeague | undefined> | undefined;

  @Input()
  teamId: number | undefined;

  isLoading$: Observable<boolean>;
  leagues$: BehaviorSubject<Array<ILeague>>;
  seasonDates$: BehaviorSubject<Array<IFixtureDate>>;
  fixture$: BehaviorSubject<IFixture | undefined>;
  activeFixture: IFixtureDate | undefined;

  activeSeasonId: number = 0;
  fixedFixtureIdx: number = 0;
  routeParamId: number | undefined;

  selectedDate: FormControl;
  MATCH_STATUS_ENUM = MATCH_STATUS_ENUM;

  isBackofficeProfile: boolean = false;

  private _subscriptions: Array<Subscription> = [];

  constructor(
    private _fb: FormBuilder,
    private _leaguesService: LeaguesService,
    private _fixtureService: FixtureService,
    private _loadingService: LoadingService,
    private _messagingService: MessagingService,
    private _router: Router,
    private _route: ActivatedRoute,
    private _appConfigService: AppConfigService
  ) {
    this.leagues$ = this._leaguesService.leagues$;
    this.isLoading$ = merge(this._leaguesService.isLoading$, this._loadingService.loading$);
    this.fixture$ = this._fixtureService.fixture$;
    this.seasonDates$ = this._fixtureService.seasonDates$;
    this.selectedDate = this._fb.control({ value: undefined, disabled: this.selectedLeague$ });

    this.isBackofficeProfile = this._appConfigService.appConfig.platform === PLATFORM_ENUM.BACKOFFICE;
  }

  ngOnInit(): void {
    if (this.selectedLeague$) {
      this._subscriptions.push(
        this.selectedLeague$.subscribe(league => {
          if (league) {
            this.loadFixtureData(league);
          }
        })
      );
    } else {
      throw new Error('League parameter not recieved by input');
    }

    this._subscriptions.push(
      this._route.params.subscribe(params => {
        if (params?.dateId) {
          this.routeParamId = +params.dateId;
        }
      }),
      this.selectedDate.valueChanges.subscribe((date: any) => {
        if (date) {
          this._changeFixtureDate(date);
        }
      }),
      this.seasonDates$
        .pipe(
          map(dates => {
            if (this.routeParamId) {
              return dates?.find(date => date.dateId === this.routeParamId);
            }
            return dates?.find(date => date.actual);
          })
        )
        .subscribe(date => {
          this.activeFixture = date;
          this.fixedFixtureIdx = date?.dateId || 0;
        })
    );
  }

  loadFixtureData(league: ILeague): void {
    const activeSeason = league.seasons.find(x => x.active);
    if (activeSeason) {
      this.activeSeasonId = activeSeason.id;
      this._fixtureService.loadSeasonDates(activeSeason.id);
      this._fixtureService.loadFixtureInfoByDate(activeSeason.id, undefined, this.teamId);
    } else {
      this._messagingService.info('FIXTURE.NO_ACTIVE_SEASON.TITLE', 'FIXTURE.NO_ACTIVE_SEASON.MESSAGE');
    }
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(x => x.unsubscribe());
  }

  // Handles the logic of displaying tabs depending on the selected
  shouldDisplayTab(tabIdx: number): boolean {
    const firstAccepted = this.fixedFixtureIdx <= 2;
    if (firstAccepted) {
      return tabIdx === 0 || tabIdx === 1 || tabIdx < 7;
    }
    const lastAccepted = this.seasonDates$.value.length - 3;
    if (this.fixedFixtureIdx >= lastAccepted) {
      return (
        tabIdx === this.seasonDates$.value.length ||
        tabIdx === this.seasonDates$.value.length - 1 ||
        tabIdx > this.seasonDates$.value.length - 8
      );
    }
    if (tabIdx > this.fixedFixtureIdx && this.fixedFixtureIdx > 3) {
      return tabIdx < this.fixedFixtureIdx + 3;
    }
    if (tabIdx > this.fixedFixtureIdx) {
      return tabIdx < this.fixedFixtureIdx + 4;
    }
    if (tabIdx < this.fixedFixtureIdx) {
      return tabIdx >= this.fixedFixtureIdx - 4;
    }
    return tabIdx === this.fixedFixtureIdx;
  }

  // Handles the change of fixture date selected
  onDateChange(fixtureDate: any): void {
    if (this.activeFixture?.dateId !== fixtureDate.dateId) {
      this.selectedDate.reset();
      this._changeFixtureDate(fixtureDate);
    }
  }

  resolveStatusClass(matchStatus: TMatchStatus): string {
    switch (matchStatus) {
      case MATCH_STATUS_ENUM.FIXTURE:
        return 'green';
      case MATCH_STATUS_ENUM.PLAYED:
        return 'dark_blue';
      case MATCH_STATUS_ENUM.PLAYING:
        return 'live_red';
      case MATCH_STATUS_ENUM.SUSPENDED:
        return 'light_blue';
      default:
        return '';
    }
  }

  goToTeam(team: ITeamMatch | undefined): void {
    if (team?.id && !this.isBackofficeProfile) {
      this._router.navigate(generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.TEAM_DETAILS, team.id));
    }
  }

  goToMatchDetails(match: IMatch): void {
    if (match.status === MATCH_STATUS_ENUM.PLAYED) {
      this._router.navigate(generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.MATCH_DETAILS, match.id));
    }
  }

  private _changeFixtureDate(fixtureDate: IFixtureDate): void {
    this.activeFixture = fixtureDate;
    this._fixtureService.loadFixtureInfoByDate(this.activeSeasonId, fixtureDate?.dateId);
  }
}
