import { Component, OnDestroy, OnInit } from '@angular/core';
import { SocialAuthService } from 'angularx-social-login';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { ConfirmationService, MenuItem } from 'primeng/api';
import { TranslateService } from '@ngx-translate/core';
import { APP_ROUTES, AuthService, ILeague, LeaguesService } from '@aft-core';
import { generateFullPrivateModuleRoute, PRIVATE_MODULE_ROUTES } from '../../../../private.routes';
import { FIXTURE_MODULE_ROUTES, generateFullFixtureModuleRoute } from '@sw-ui-components/modules/fixture/fixture.routes';

@Component({
  selector: 'aft-scout-private-header',
  templateUrl: './private-header.component.html',
  styleUrls: ['./private-header.component.scss'],
})
export class PrivateHeaderComponent implements OnInit, OnDestroy {
  leaguePageRoute: Array<string | number>;
  teamPageRoute: Array<string | number>;
  playerPageRoute: Array<string | number>;
  advSearchPageRoute: Array<string | number>;
  displaySidebar: boolean = false;
  items: MenuItem[] = [];
  private _subscriptions$: Subscription[] = [];
  private leagues: ILeague[] = [];

  constructor(
    private _router: Router,
    private _authService: AuthService,
    private _socialAuthService: SocialAuthService,
    private _confirmationService: ConfirmationService,
    private _translateService: TranslateService,
    private _leaguesService: LeaguesService
  ) {
    this.leaguePageRoute = generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.LEAGUE);
    this.teamPageRoute = generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.TEAM_DETAILS);
    this.playerPageRoute = generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.PLAYER);
    this.advSearchPageRoute = generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.ADVANCED_SEARCH);
  }

  ngOnInit(): void {
    this._subscriptions$.push(
      this._leaguesService.leagues$.subscribe(leagues => {
        this.leagues = leagues;
        this.initMenuItems();
      })
    );
  }

  initMenuItems(): void {
    this.items = this.leagues.map(league => {
      const item: MenuItem = {
        label: league.name,
        command: () => {
          this.goToLeagues(league.id);
        },
      };
      return item;
    });
  }

  ngOnDestroy(): void {
    this._subscriptions$.forEach(sub => sub.unsubscribe());
  }

  onLogout(): void {
    this._confirmationService.confirm({
      header: this._translateService.instant('HEADER.LOGOUT.MODAL_HEADER'),
      message: this._translateService.instant('HEADER.LOGOUT.MODAL_BODY'),
      acceptLabel: this._translateService.instant('HEADER.LOGOUT.MODAL_ACCEPT'),
      rejectLabel: this._translateService.instant('GENERAL.CANCEL'),
      accept: () => {
        if (this._authService.isPlatformLoggedIn$.value) {
          this._socialAuthService.signOut();
        }
        this._authService.logout();
      },
    });
  }

  toggleSidebar(): void {
    this.displaySidebar = !this.displaySidebar;
  }

  goToProfile(): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.PROFILE));
    this.displaySidebar = false;
  }

  goToAdvancedSearch(): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.ADVANCED_SEARCH));
    this.displaySidebar = false;
  }

  goToFixture(): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.FIXTURE));
    this.displaySidebar = false;
  }

  goToFavorites(): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.FAVORITES));
    this.displaySidebar = false;
  }

  goToLeagues(leagueId: number): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.LEAGUE, leagueId));
  }

  goToHomepage(): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.HOME));
  }

  shouldShowSearchbar(): boolean {
    return APP_ROUTES.PRIVATE[0] !== this._router.url.replace('/', '');
  }
}
