import { Injectable } from '@angular/core';
import { ILeague, LeaguesService, STORAGE_KEYS_ENUM } from '@aft-core';
import { BehaviorSubject } from 'rxjs';
import { take } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class HomeService {
  leagues$: BehaviorSubject<ILeague[]>;

  selectedLeague$: BehaviorSubject<ILeague | undefined>;
  private _storageKey = STORAGE_KEYS_ENUM.HOME_LEAGUE;

  constructor(private _leaguesService: LeaguesService) {
    this.leagues$ = this._leaguesService.leagues$;
    this.selectedLeague$ = new BehaviorSubject<ILeague | undefined>(undefined);

    // Pre selects the first league returned from the league service
    this.leagues$.pipe(take(2)).subscribe(leagues => {
      if (leagues.length) {
        const localStorageVal = localStorage.getItem(this._storageKey);
        if (localStorageVal) {
          const league = this.leagues$.value.find(l => l.id === +localStorageVal);
          if (league) {
            this.selectedLeague$.next(league);
            return;
          }
        }
        if (!this.selectedLeague$.value) {
          this.selectedLeague$.next(leagues[0]);
        }
      }
    });
  }

  changeSelectedLeague(league: ILeague): void {
    localStorage.setItem(this._storageKey, league.id.toString());
    this.selectedLeague$.next(league);
  }
}
